import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { Button, ModalR } from '../common';
import clsg from './CustomGeneric.module.scss';
import cls from './CustomHR.module.scss';

import { updateComponent, updateComponentItem } from '../../services/components.service';
import ModuleLists from '../SideMenuEntries/ModulesComp/ModuleLists';
import { getFilteredUsers, getUsersByPositionsV2 } from '../../services/user.service';
import { first } from 'rxjs';
import { add } from '../../assets/images/icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarDays, faRectangleXmark, faTrash } from '@fortawesome/free-solid-svg-icons';
import UserCard from '../common/UserCard/UserCard';
import moment, { now } from 'moment';
import { store } from '../../reduxStore/store';
import { setCompanyProcesses, setMessageBar, setProcessModule, setUsersArray } from '../../reduxStore/actions/flowActions';
import { APPEND_TO_DATA, generateNewUUID } from '../../shared/utility';
import { processTaskAction, useProcessTaskContext } from '../../context/processTask.context';
import { getProcessById } from '../../services/processes.service';

import * as R from 'ramda';
import { Scheduler } from "@aldabil/react-scheduler";
import { navigateToTransaction } from '../SideMenuEntries/DashboardMobile';
import SelectAndMultiselect from '../SideMenuEntries/ProcessesComps/SelectAndMultiselect/SelectAndMultiselect';
import { purchase } from '../../services/payments.service';
import DatePicker from 'react-datepicker';
import { eachDayOfInterval, format, startOfWeek } from "date-fns";

const CustomHR = (props) => {

    const { begunModule, allModules, companyData, companyUsersArray, goto, components, authenticatedUser, user, companyProcesses, template } = props;
    const { title, selectedComponent, setSelectedComponent, step } = props;
    const [loading, setLoading] = useState(false);

    return <div className={clsg.accordionContainer}>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '18px' : '20px', fontWeight: 'bold', marginTop: isMobile ? '' : '50px' }}>
            {title}
        </div>
    </div>;
}

const mapStateToProps = store => {
    const {
        authenticatedUser: { position },
        authenticatedUser,
    } = store.authReducer;
    const { user } = store.sessionReducer;

    const { begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, companyProcesses } = store.flowReducer;
    return {
        begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, authenticatedUser, user, companyProcesses
    };
};


export default connect(mapStateToProps)(CustomHR);

export const CustomHRTimesheetStep = (props) => {
    const { selectedComponent, begunModule, goto } = props;
    const { companyData, companyUsersArray, components } = useSelector(store => store.flowReducer);
    const { authenticatedUser } = useSelector(store => store.authReducer);

    const [loading, setLoading] = useState(false);
    const [items, setItems] = useState(selectedComponent?.component_items?.length > 0 ? selectedComponent.component_items.filter(it => format(it.value, 'MM/yyyy') === format((new Date()), 'MM/yyyy')) : [{ item_code: '', value: '', alt_value: '', id: generateNewUUID() }])

    const [filter, setFilter] = useState('month');

    const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();

    const addItemButton = () => (
        <Button
            light
            style={{ display: '' }}
            clicked={() => {
                setItems([...items, { item_code: '', value: '', alt_value: '', id: generateNewUUID() }])
            }}
            icon={<img src={add} />}
        />
    );

    const DateInput = props => {

        return (

            <div style={{ width: '100%', height: 'auto', border: '0' }}>

                <DatePicker
                    selected={props?.value ? new Date(props?.value) : new Date()}
                    showTimeSelect
                    timeFormat="HH:mm"
                    timeIntervals={5}
                    customInput={
                        <div
                            style={{
                                cursor: 'pointer',
                                display: 'flex',
                                justifyContent: 'center',
                                width: '100%',
                                alignItems: 'stretch',
                                padding: '5px',
                                border: '0px solid var(--app-color-gray-lighter)',
                            }}
                        >
                            <FontAwesomeIcon icon={faCalendarDays} style={{ margin: '0 10px', width: '20px', height: '20px' }} />
                            <input
                                value={props.value ? moment(props.value).format('DD MMMM YYYY HH:mm') : ''}
                                className={cls.inputValue}
                                type="text"
                                style={{
                                    flex: 1,
                                    marginTop: 'auto',
                                    marginBottom: 'auto',
                                    outline: 'none',
                                    fontFamily: 'var(--app-text-main-font)',
                                    fontSize: '16px',
                                    lineHeight: '20px',
                                    color: '#4A4A4A',
                                    textAlign: 'left',
                                    maxWidth: '98%',
                                    border: '0px'

                                }}
                                onChange={_e => { }}
                            />

                        </div>
                    }
                    className={cls.singleInputContainer}
                    onChange={date => {
                        props.handleDate(date);
                    }}
                />
            </div>
        )
    }

    return <div className={clsg.accordionContainer}>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '18px' : '20px', fontWeight: 'bold', marginTop: isMobile ? '' : '50px', padding: '20px' }}>
            <div style={{ display: 'flex', justifyContent: 'flex-end', width: '100%', padding: '20px' }}>

                <div style={{ display: 'flex' }}>
                    <SelectAndMultiselect
                        value={filter}
                        titleProps={{ fontSize: '12px', color: 'var(--app-color-gray-light)' }}
                        className={cls.multiselectComponent}
                        visible={true}
                        mandatory={false}
                        items={[{ label: 'Today', value: 'today' }, { label: 'This Week', value: 'week' }, { label: 'This Month', value: 'month' }, { label: 'Select Month', value: 'select' }]}
                        onChange={(value) => {
                            const filterDataToWeek = (data, formatting = 'd/MM/yyyy', currDay) => {
                                const end = currDay ?? new Date();
                                const start = startOfWeek(end, { weekStartsOn: 1 });
                                const daysInWeekSoFarArr = eachDayOfInterval({ start, end }).map((day) =>
                                    format(day, formatting)
                                );
                                return data.filter((i) =>
                                    daysInWeekSoFarArr.includes(`${format(i.value, formatting)}`)
                                );
                            };
                            if (value === 'week') {
                                setItems(filterDataToWeek(selectedComponent.component_items));
                            } else if (value === 'today') {
                                const today = new Date();
                                setItems(selectedComponent.component_items.filter(it => format(it.value, 'd/MM/yyyy') === format(today, 'd/MM/yyyy')))
                            } else if (value === 'month') {
                                const today = new Date();
                                setItems(selectedComponent.component_items.filter(it => format(it.value, 'MM/yyyy') === format(today, 'MM/yyyy')))
                            }
                            setFilter(value)
                        }}
                    />
                    {filter === 'select' &&
                        <SelectAndMultiselect
                            titleProps={{ fontSize: '12px', color: 'var(--app-color-gray-light)' }}
                            className={cls.multiselectComponent}
                            visible={true}
                            mandatory={false}
                            items={[{ label: 'January', value: '01' }, { label: 'February', value: '02' }, { label: 'March', value: '03' },
                            { label: 'April', value: '04' }, { label: 'May', value: '05' }, { label: 'June', value: '06' },
                            { label: 'July', value: '07' }, { label: 'August', value: '08' }, { label: 'September', value: '09' },
                            { label: 'October', value: '10' }, { label: 'November', value: '11' }, { label: 'December', value: '12' }]}
                            onChange={(value) => {
                                setItems(selectedComponent.component_items.filter(it => format(it.value, 'MM') === value))
                            }}
                        />}
                </div>
            </div>
            {selectedComponent?.id ? <ModuleLists
                className={cls.itemsTable}
                columns={['Start Time', 'End Time', 'Work Details', 'Duration', 'Options']}
                list={items?.map(itm => {
                    return {
                        ['Start Time']: <DateInput value={itm.value} style={{ width: '100%', border: '1px solid black' }}
                            handleDate={date => {
                                itm.value = date;
                                setItems([...items.filter(it => it.id !== itm.id), itm]);
                            }}
                        />,
                        ['End Time']: <DateInput value={itm.alt_value} style={{ width: '100%', border: '1px solid black' }}
                            handleDate={date => {
                                itm.alt_value = date;
                                setItems([...items.filter(it => it.id !== itm.id), itm]);
                            }}
                        />,
                        ['Work Details']: <input defaultValue={itm.item_code}
                            className={cls.inputValue}
                            style={{
                                fontWeight: 'normal',
                            }}
                            onChange={e => {
                                itm.item_code = e.target.value
                            }}
                            onBlur={() => {
                                setTaskProcessContext({
                                    action: processTaskAction.setModuleItems,
                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                });
                            }}
                        />,
                        ['Duration']: <input
                            defaultValue={!(itm.value || itm.alt_value) ? '0' : moment(new Date(itm.alt_value), 'DD/MM/YYYY').diff(moment(new Date(itm.value), 'DD/MM/YYYY'), 'hours')}
                            className={cls.inputValue}
                            style={{
                                fontWeight: 'normal',
                            }}
                            readOnly
                        />,
                        ['Options']: <FontAwesomeIcon icon={faTrash} onClick={() => {
                            setTaskProcessContext({
                                action: processTaskAction.setModuleItems,
                                value: items.filter(it => it.id !== itm.id),
                            });
                            setItems(items.filter(it => it.id !== itm.id));
                        }} />,
                        id: itm.id
                    };
                })}
                footer={[<br key={0} />, <br key={1} />, <input value={'Total'} key={2} className={cls.inputValue} readOnly />, <input key={3} readOnly className={cls.inputValue}
                    defaultValue={
                        `${R.reduce((a, b) => +a + (!(b.value || b.alt_value) ? 0 : moment(new Date(b.alt_value), 'DD/MM/YYYY').diff(moment(new Date(b.value), 'DD/MM/YYYY'), 'hours')), 0, items ?? [])}`} />, <br key={4} />]}
            /> : props.children}
            <div style={{ display: 'flex', justifyContent: 'left' }}>
                {addItemButton()}
            </div>
        </div>



    </div>;


}
